import React from 'react'
import "./featured.css"

const Featured = ({ img, description, link }) => {
    return (
        <div className="featured">
            <img src={img} alt="featured" />
            <div className="featured-description">
                <p>{description}</p>
                <button>{link}</button>
            </div>
        </div>
    )
}

export default Featured
